.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    padding: 5rem 0 5rem 0;
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
    box-shadow: none;
}

.project-card img{
    width: 100%;
}

.project-card:hover{
    box-shadow: 0 0 13px #fff;
    transition: 1s;
}

.project-title{
    color: #fff;
    padding: 1rem 0;
}

.project-details p{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}

.project-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}

.project-btns .btn{
    padding: 0.5rem 1rem;
    margin: auto;
}

@media screen and (max-width: 740px) {
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    } 
}

